@charset "UTF-8";

@font-face {
  font-family: "alfianjau";
  src:url(".././alfianjau.eot");
  src:url(".././fonts/alfianjau.eot?#iefix") format("embedded-opentype"),
    url(".././fonts/alfianjau.woff") format("woff"),
    url(".././fonts/alfianjau.ttf") format("truetype"),
    url(".././fonts/alfianjau.svg#alfianjau") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "alfianjau" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="i-"]:before,
[class*=" i-"]:before {
  font-family: "alfianjau" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

