
.e-menu {
    position : fixed;
    z-index : 2;
    top : 31.2vh;
    right : 5%;
    // transform: rotate(45deg);
    .stretchy-nav-bg {
        /* this is the stretching navigation background */
        position : absolute;
        z-index : 1;
        top : 0;
        right : 0;

        width : 60px;
        height : 60px;
        // background: $brand-primary;
        // box-shadow: 0 1px 4px rgba(#000, .2);

        transition : height .2s, box-shadow .2s;

        border-radius : 10px;
    }
    &.nav-is-visible .stretchy-nav-bg {
        height : 100%;
        // box-shadow: 0 6px 30px rgba(#000, .2);
    }
    &.nav-is-visible .cd-nav-trigger {
        @include hover-shade;
    }
}

.cd-nav-trigger {
    position : absolute;
    z-index : 3;
    top : 0;
    right : 0;
    /* replace text with image */

    overflow : hidden;

    width : 60px;
    height : 60px;

    transition : .3s;
    transform : rotate(45deg);
    white-space : nowrap;

    color : transparent;
    border-radius : 10px;
    // background : $primary;

    @include alt-gradient;
    @include mild-shade;
    .cd-nav-trigger-icon , .cd-nav-trigger-icon::after , .cd-nav-trigger-icon::before {
        position : absolute;

        width : 16px;
        height : 2px;
        /* this is the hamburger icon */

        content : '';

        background-color : #ffffff;

        @include mq($until : desktop) {
            transform : translateX(initial) rotate(initial);
        }
    }
    .cd-nav-trigger-icon {
        top : 50%;
        right : auto;
        bottom : auto;
        /* middle line of the hamburger icon */
        left : 50%;

        transition : background-color .2s;
        transform : translateX(-50%) translateY(-50%) rotate(-45deg);

        @include mq($until : desktop) {
            transform : translateX(-50%) translateY(-50%) rotate(0);
        }
    }
    .cd-nav-trigger-icon::after , .cd-nav-trigger-icon::before {
        position : absolute;
        top : 0;
        left : 0;
        /* top and bottom lines of the hamburger icon */

        content : '';
        transition : transform .2s;

        backface-visibility : hidden;
    }
    .cd-nav-trigger-icon::before {
        transform : translateY(-6px);
    }
    .cd-nav-trigger-icon::after {
        transform : translateY(6px);
    }
    .no-touch &:hover ~ .stretchy-nav-bg {
        box-shadow : 0 6px 30px rgba(#000000, .2);
    }
    .nav-is-visible & {
        .cd-nav-trigger-icon {
            background-color : transparent;
        }
        .cd-nav-trigger-icon::before {
            content : '';
            transform : rotate(-45deg);
        }
        .cd-nav-trigger-icon::after {
            transform : rotate(45deg);
        }
    }
}

.e-menu.nav-is-visible {
    @include mq($until : desktop) {
        height : 100% !important;
    }
}

.e-menu ul {
    position : relative;
    z-index : 2;

    visibility : hidden;

    height : 0;
    padding : 3em 0 0;

    list-style : none;

    transition : visibility .3s, height .3s ease-in;
    text-align : right;
    a {
        font-size : 1.4rem;
        line-height : 1em;

        position : relative;

        display : block;

        height : 50px;
        padding : 0 calc(1em + 60px) 0 1em;
        padding-top : .7em;
        // transition : color .2s;

        color : rgba(#ffffff, .7);
        &:hover .cd-stretchy-nav-label {
            opacity : 1;

            @include hover-shade;
        }
    }
    .cd-stretchy-nav-icon {
        position : absolute;
        top : 2em;
        right : 0;

        width : 60px;
        height : 60px;

        transform : translateY(-50%) scale(0);

        @include gradient;
        @include alt-gradient;
        // opacity : .6;
    }
    .cd-stretchy-nav-label {
        font-size : 1rem;
        font-weight : 300;
        font-style : initial;

        display : inline;

        padding : 5px 10px;

        transform : translateX(-25px);
        text-transform : uppercase;

        opacity : 0;
        border-radius : 5px;
        background-color : rgba(0, 0, 0, .5);

        @include mq($until : desktop) {
            opacity : 1 !important;
            background-color : transparent;
        }
    }
}

.e-menu.nav-is-visible ul {
    visibility : visible;

    height : 100%;
    .cd-stretchy-nav-icon {
        // animation: slideIn .15s ;
        // transition : opacity .2s;
        transform : translateY(-50%) scale(1);
    }
    .cd-stretchy-nav-label {
        transition : opacity .3s;
        transform : translateX(0);
        // animation : slideIn .15s backwards;

        opacity : 0;

        @include mq($until : desktop) {
            font-weight : 800;

            color : white;
            box-shadow : none;
        }
        @include mild-shade;
    }
    li {
        margin-top : 0;
        margin-bottom : 35px;
    }
    li a span {
        width : 60px;
        height : 60px;
        &.i-icon {
            line-height : 30px;

            position : absolute;
            top : 0;

            display : table;

            width : 60px;
            height : 60px;
            padding : 10px;
            // transition : box-shadow .3s ease-in;

            transform : rotate(45deg);
            text-align : center;

            border-radius : 10px;
            // box-shadow: 0 6px 30px rgba(#000, .2);
            background-color : $primary;

            @include mq($until : desktop) {
                display : none;
            }
            @include mild-shade;
        }
    }
    a:hover {
        text-decoration : none;

        color : #ffffff;
        .cd-stretchy-nav-icon {
            opacity : 1;

            @include hover-shade;
        }
    }
    .i-icon:before {
        display : block;
        display : table-cell;

        transform : rotate(-45deg);
        vertical-align : middle;
    }
    // @for $i from 1 through 5 {
    //     li:nth-of-type(#{$i}) .cd-stretchy-nav-icon , li:nth-of-type(#{$i}) .cd-stretchy-nav-label {
    //         animation-delay : (.05s * ($i + 1));
    //     }
    // }
}


.is-static .e-menu.nav-is-visible ul {
    @include mq($until : desktop) {
        display : block;
    }
}

.is-static {
    .cd-nav-trigger {
        display : none;

        @include mq($until : desktop) {
            top : 10px;
            left : 0;

            display : block;

            width : 60px;
            height : 60px;
            margin : auto;

            transform : rotate(0);
            //             box-shadow : none;
        }
    }
    .e-menu {
        top : 0;
        bottom : 0;

        height : 37vh;
        margin : auto;

        @include mq($until : desktop) {
            position : absolute;
            right : 0;
            bottom : auto;

            width : 100%;
            height : 50px;
                z-index: 110;
        }
        ul {
            visibility : visible;

            margin-top : 0;
            padding-top : 0;

            @include mq($until : desktop) {
                right : 0;
                left : 0;

                display : none;

                height : 100%;
                margin : auto !important;
                padding-top : 100px;

                background-color : $primary;
            }
            li {
                margin-bottom : 1em;

                @include mq($until : desktop) {
                    border-top : 4px solid white;
                    border-bottom : 4px solid white;
                    // background-color : $primary;

                    @include alt-gradient-transformed;
                }
                a {
                    line-height : .5em;

                    padding-right : calc(1em + 20px);

                    @include mq($until : desktop) {
                        padding-right : 0;
                        padding-left : 0;

                        text-align : center;
                    }
                }
            }
            .i-icon {
                line-height : 20px;

                position : absolute;
                top : 0;

                display : table;

                width : 1em !important;
                width : 60px;
                height : 1em !important;
                height : 60px;
                padding : 10px;
                // transition : box-shadow .3s ease-in;

                transform : rotate(45deg);
                text-align : center;

                border-radius : 10px;
                // box-shadow: 0 6px 30px rgba(#000, .2);
                background-color : $primary;

                &:hover {
                    @include hover-shade;
                }

                @include mq($until : desktop) {
                    display : none;
                }
                @include mild-shade;
                &:before {
                    display : block;

                    transform : rotate(-45deg);
                }
            }
        }
    }
}


@include mq($until : desktop) {
    .e-page--blog .cd-nav-trigger {
        position : fixed;
        top : 10px;
        right : 0;
        left : 0;

        margin : auto;

        transform : rotate(0);
    }
    .e-page--blog .e-menu {
        top : 0;
        right : 0;

        width : 100%;
        ul {
            width : 100%;
            margin : 0;
            margin-top : 80px;
            padding : 0;

            background-color : $primary;
            li {
                width : 100%;

                text-align : center;
                    // background-color : $primary;

                    @include alt-gradient-transformed;
                a {
                    line-height : 40px;

                    padding : 0;

                    border-top : 4px solid $highlight;
                    border-bottom : 4px solid $highlight;
                }
            }
        }
    }
    .e-page--blog .e-menu.nav-is-visible {


        background-color : $primary;
    }
}


@keyframes scaleIn {
    from {
        transform : rotate(45deg) translateY(-50%) scale(0);
    }
    to {
        transform : rotate(45deg) translateY(-50%) scale(1);
    }
}

@keyframes slideIn {
    from {
        transform : rotate(45deg) translateX(-25px);

        opacity : 0;
    }
    to {
        transform : rotate(45deg) translateX(0);

        opacity : 1;
    }
}
