.c-icon {
    position: absolute;
    height: 60%;
    width: 60%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    padding: 10px;
    border-radius: 10px;
    @include mild-shade;
    &:hover{
        @include hover-shade;
    }
}

.c-icon__wrapper {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: 50%;
    width: 50%;
    font-size: 2em;
    @include mq($until: desktop) {
        font-size: 1.5em;
    }
}