/* ==========================================================================
   #WRAPPERS
   ========================================================================== */

/**
 * Page-level constraining and wrapping elements.
 */

$inuit-wrapper-width: 1200px !default;

/* stylelint-disable */
@if (type-of($inuit-wrapper-width) != number) {
  @error "`#{$inuit-wrapper-width}` needs to be a number."
}
/* stylelint-enable */


.o-wrapper {
  @include inuit-clearfix();
  padding-right: $inuit-global-spacing-unit;
  padding-left:  $inuit-global-spacing-unit;
  margin-right: auto;
  margin-left:  auto;
  max-width: $inuit-wrapper-width;
}





/* Size variants.
   ========================================================================== */

.o-wrapper--tiny {
  padding-right: $inuit-global-spacing-unit-tiny;
  padding-left:  $inuit-global-spacing-unit-tiny;
}

.o-wrapper--small {
  padding-right: $inuit-global-spacing-unit-small;
  padding-left:  $inuit-global-spacing-unit-small;
}

.o-wrapper--large {
  padding-right: $inuit-global-spacing-unit-large;
  padding-left:  $inuit-global-spacing-unit-large;
}

.o-wrapper--huge {
  padding-right: $inuit-global-spacing-unit-huge;
  padding-left:  $inuit-global-spacing-unit-huge;
}

.o-content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    // overflow-y: scroll;
}

.o-content--static {
    overflow: hidden;
    height: 100vh;

    @include mq($until: desktop) {
      width: 100%;
      overflow-y: scroll;
    justify-content: flex-start;
    }
}

.is-static .o-content--static {
    @include mq($until: desktop) {
      width: 100%;
      overflow: scroll;
          flex-direction: column;
    justify-content: flex-start;
    padding: 10vh 0;
    }
}

.hire .o-content--static {
    @include mq($from: tablet) {
      justify-content:center;
    }
}

.o-wrapper {
    width : 100vw;
    margin : 20px auto;

    color : $highlight;

    @include mq($until : tablet) {
        width : 80vw;
        height : 50vh;
    }

    @include mq($from: tablet, $until: desktop) {
      width: 80vw;
    }
}

.who .o-wrapper {
    @include mq($until : tablet) {
        width : 100%;
        padding: 0;
    }
}

.hire .o-wrapper {
    @include mq($until : tablet) {
        width : 100vw;
        padding: 0;
    }
}

.e-page--blog .o-wrapper {
    @include mq($until: tablet) {
    height: 100%;
    width: 100%;
    margin: 0;
    overflow: hidden;
    display: block;
    padding: 15px;
    }
}

.o-wrapper--rotated {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    @include mq($from: mobile, $until: tablet) {
      transform: rotate(0);
    }
}

.o-wrapper--footer {
  margin-bottom: 0;
  margin-top: 0;
}


.o-sticky-layout {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    left: 0;
    top: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
            justify-content: space-between;
}


.o-sticky-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

.o-sticky-wrapper:first-child .c-boxed:before {
    left: $ruled;
}

.o-sticky-wrapper:last-child .c-boxed:before {
    right: $ruled;
}

.o-sticky-wrapper .c-boxed:first-child:after {
    top: $ruled;
}

.o-sticky-wrapper .c-boxed:last-child:after {
    bottom: $ruled;
}

.o-sticky-wrapper:first-child .c-boxed:last-child:before {
    bottom: 0;
}

.o-sticky-wrapper:last-child .c-boxed:first-child:after {
    right: 0;
}

.o-sticky-wrapper:last-child .c-boxed:last-child:before {
    bottom: 0;
}

.o-sticky-wrapper:last-child .c-boxed:last-child:after {
    right: 0;
}

.o-row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    @include mq($until: tablet) {
      position: static;
    }
  }

@media all and (min-width: 500px) {
  .o-wrapper {
    padding: 0 20px;
    /*   background-color: ; */
    padding-top: 20px;
    border-radius: 5px;
  }
  
  .o-row--wrapped {
    flex-wrap:wrap;
  }

}

@media all and (min-width: 1000px) {
  .o-wrapper {
    max-width: 1000px;
  }
}
