[class*="col-"] {
	margin-bottom: 10px;
    min-height: 30px;
    text-align: center;
    padding: 5px;
    margin-right: 10px;
    position: relative;
    @include mq($until: tablet) {
      position: static;
    }
}


[class*="col-"]:last-child {
	margin-right: 0;
}

.col-1 {
	flex: 1;
}

.col-2 {
	flex: 2;
}

.col-3 {
	flex: 3;
}

.col-4 {
	flex: 4;
}

.col-5 {
	flex: 5;
}
