.o-layout--paper {
    display : block;

    background : $highlight;

    @include global-padding;
    @include mild-shade;
    @include round-radius;

    .o-layout--fluid {
    @include mq($until: desktop) {
        width: 100% !important;
    }
}
}

.o-layout--nopadding {
    height : 100%;

    @include reset-padding;

    @include mq($until: desktop) {
        height: initial;
    }
}

.o-layout .o-layout__item {
	@include mq($until: desktop) {
        width: 100% !important;
        text-align:center;
            
        @include global-padding;
    }
}

.hire .o-layout__item {
	@include mq($until: desktop) {
            
        padding:0;
    }
}

.who .o-layout__item {
	text-align:center;
}

