.o-block__footer {
  position: relative;

  display: inline-block;

  width: 80px;
  height: 80px;
  padding: .5em;

  cursor: pointer;
  transition: box-shadow .3s;
  text-align: center;

  color: $highlight;
  background-color: $primary;

  @include gradient;
  @include alt-gradient;
  @include mild-shade;
  @include round-radius;
  @include transformed;

  &:hover {
    @include hover-shade;
  }

  a {
    font-size: 1.2em;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: block;

    height: 1em;
    margin: auto;

    color: $highlight;

    @include alt-transformed;
  }
}

.o-block--paper {
  text-align: left;
  overflow: auto;
  height: 600px;

  @include global-padding;

  h3 {
    margin-top: 10px;
  }
}

.who .o-block--paper {
  @include mq($from: tablet) {

    padding: 2em;
  }
}

.o-block--padded {
  padding: 2em;

  @include mq ($until : desktop) {
    padding: 1.5em;
  }
}

.o-block--slider {
  position: relative;

  display: block;
  // width: 30em;

  margin: 0 auto;

  text-align: center;

  @include round-radius;
}

.o-block--colored {
  font-size: 1.5em;
  font-weight: 800;

  height: 100%;

  text-transform: uppercase;

  border-radius: 0 10px 10px 0;
  // @include bg-gradient;
  background-color: $accent-lighting;

  @include mq ($until : desktop) {
    border-radius: 0 0 10px 10px;
  }

  p {
    color: $highlight;

    @include mq ($until : desktop) {
      font-size: 16px;

      border-radius: 0 0 10px 10px;
    }
  }
}

.o-block--icon {
  padding: 1em;

  cursor: pointer;

  .i-icon {
    @include gradient;
    @include alt-gradient-transformed;
    @include round-radius;
    @include global-padding;
    @include mild-shade;

    &:hover {
      @include hover-shade;
    }
  }
}



.o-block--overlapped {
  margin-top: -6em;
  // @include global-padding;

  @include mq ($until: desktop) {
    margin-top: 0;
  }

  @include mild-shade;
  @include alt-gradient-transformed;

  span {
    position: relative;

    display: block;
    overflow: hidden;

    width: 100%;
    height: 0;
    padding-bottom: 80%;

    @include mq ($until : tablet) {
      width: 250px;
    }

    @include round-radius;

    &:after {
      content: '';
      background: #00bcd4;
      opacity: 0.4;
      width: 500px;
      height: 500px;
      z-index: 100;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }
  }
}

.o-block .o-block__footer {
  @include mq($until : tablet) {
    width: 40px;
    height: 40px;
  }
}
