.c-form{
	@include round-radius;
	// @include global-padding;
}

.c-form__title {
	text-transform: uppercase;
	font-weight: 800;
	h3 {
		margin-top: 1em;
		color: $accent-lighting;
		@include word-shade;

		@include mq ($until: desktop) {
			font-size: 1.55em;
		}
	}

}

.c-form__item{
	@include round-radius;
	padding: .5em 1em;
	margin: .5em 0;
	width: 100%;
	border: 0;
	@include mild-shade;

	&:focus{
		outline-style: solid;
		outline-color: $accent-lighting;
		outline-width: medium;
		@include hover-shade;
	}
}