$steel : #e0e0e0;

.c-shape {
    a {
        text-decoration : none;

        color : #ffffff;
    }
    a:hover {
        text-decoration : underline;

        color : white;
    }
}

.c-shape__wrapper {
    position : relative;

    height : 400px;
    //     margin-top : -1em;

    @include mq ($until : tablet) {
        margin-top : 10vh;
    }
    .c-shape__block--screen {
        position : absolute;
        z-index : 1;
        right : 0;
        left : 0;
        /* width : 50vw;*/

        width : 650px;
        height : auto;
        margin : 0 auto 0;

        border-radius : 10px 10px 0 0;
        background-color : $steel;

        @include mq($until : desktop) {
            width : 100%;
            //         	width: 100%;
            height : auto;
        }
        &:after {
            font-weight : 800;
            line-height : 50px;

            position : absolute;
            top : 100%;
            right : 0;
            left : 0;

            height : 50px;

            content : 'MY CASES';
            text-align : center;

            color : $accent-lighting;
            border-top : 1px solid white;
            border-radius : 0 0 10px 10px;
            background-color : $steel;
            box-shadow : 3px 5px 6px 0 rgba(158, 158, 158, .34);

            @include mq ($until : desktop) {
                content : '';
            }
        }
        &:before {
            position : absolute;
            z-index : -10;
            top : 25%;
            right : 0;
            left : 0;

            display : block;

            width : 300px;
            height : 430px;
            margin : 0 auto;

            content : '';
            -webkit-transform : scaleY(.06);
                    transform : scaleY(.06);

            border-radius : 100%;
            background-color : transparent;
            box-shadow : 0 2000px 200px rgba(0, 0, 0, .2), 0 2000px 200px rgba(0, 0, 0, .1), 0 2000px 200px rgba(0, 0, 0, .09);
            // box-shadow : 0 135em 200px 0 rgba(0, 0, 0, .25);

            @include mq($until : tablet) {
                height : 0;
            }
        }
    }
    .c-shape__block--glass {
        position : relative;

        display : block;

        width : 100%;
        //     height: 0;
        height : 100%;
        //     padding-bottom: 56%;
        padding : 10px;

        border-radius : inherit;
        background-color : #000000;
    }
    .c-shape__block--stand {
        position : absolute;
        z-index : -1;
        top : 100%;
        right : 0;
        bottom : 0;
        left : 0;

        width : 150px;
        height : 80px;
        margin : 0 auto;

        background-color : $steel;

        @include mq($until : desktop) {
            z-index : 5;

            width : 30px;
            height : 30px;
            margin-top : 10px;

            border : 2px solid #8f8f8f;
            border-radius : 100%;
            background-color : #fafafa;
        }
        &:before , &:after {
            position : absolute;
            z-index : 0;
            top : 58px;

            display : block;

            width : 150px;
            height : 40px;

            content : '';

            background : $steel;

            @include mq($until : desktop) {
                display : none;
            }
        }
        &:before {
            left : -25px;

            -webkit-transform : skewx(-50deg);
                    transform : skewx(-50deg);

            border-radius : 0 0 0 20px/10px;
            box-shadow : -2px 2px 2px rgba(250, 251, 253, .03), 0 2px 0 #8f8f8f, inset 0 -2px 20px rgba(250, 251, 253, .03);
        }
        &:after {
            right : -25px;

            -webkit-transform : skewx(50deg);
                    transform : skewx(50deg);

            border-radius : 0 0 20px/10px 0;
            box-shadow : 2px 2px 2px rgba(250, 251, 253, .03), 0 2px 0 #8f8f8f, inset 0 -2px 20px rgba(250, 251, 253, .03);

            @include mq($until : desktop) {
                line-height : 50px;

                position : fixed;
                top : auto;
                right : 0;
                bottom : 1.6em;
                left : 0;

                display : block;

                width : 80px;
                height : 50px;
                margin : 0 auto;

                content : 'MY CASES';
                transform : skewx(0);
                // @include alt-gradient;

                @include mild-shade;
                @include gradient--reversed;
                @include round-radius;
            }
        }
    }
}
