.has-image{
	overflow:hidden;
}


.is-gradient {
	transition: all .3s ease-in;
	
	&:hover {
		@include gradientBg(-45deg, $accent, $accent-lighter);
	}
	&:focus, &:active {
		@include gradientBg(-45deg, $accent-lighter, $accent);
	}
	&:focus {
		// outline: 2px solid $neon;
		// box-shadow: 5px 8px 20px 0px rgba(59,52,33,.3);
		@include mild-shade;
	}
	&:active {
		filter: brightness(.85) contrast(1.3);
	}
}