.e-slider {
    z-index : 10;
    // max-width : 500px;

    margin : auto;

    @include round-radius;
    @include mild-shade;
    button {
        width : 50px;
        height : 50px;

        // @include transformed;
        @include round-radius;
        @include gradient--reversed;
        // @include alt-gradient;
        @include mild-shade;
        &:hover {
            @include hover-shade;
        }
    }
    .slick-slide {
        
    }
    .slick-prev {
        left : -20%;
        @include mq($until: desktop) {
            left:20%;
        }
    }
    .slick-next {
        right : -20%;
        @include mq($until: desktop) {
            right:20%;
        }
    }
    .slick-arrow {
        @include mq($until: desktop) {
            bottom: 0;
    top: auto;
    position: fixed;
    margin: 0 auto;
        }
        &:before {
            font-size : 50px;

            // position : absolute;
            // top : 0;
            // right : 0;
            // bottom : 0;
            // left : 0;

            display : block;

            margin : auto;

            color : $highlight;

            // @include alt-transformed;
        }
    }
    .slick-next:before {
        top : -2px;
        left : 5px;

        content : 'p';
    }
    .slick-prev:before {
        bottom : -2px;
        left : -5px;

        content : 'q';
    }
    .slick-dots {
        margin-top : 1em;
        button {
            width : 30px;
            height : 30px;
            &:before {
                font-size : 30px;

                width : 30px;
                height : 30px;

                content : 'h';

                color : $primary;
            }
        }
    }
}
