.e-footer {
    background : $primary;

    @include box-shadow(8px , 10px ,20px, rgba(46,61,73,.1), true);
    @include mq($until: desktop) {
        height: 150px;
    }
}

.e-footer__element {
    display : flex;

    margin-bottom : 1rem;
    padding : 1em 8em;

    color : $highlight;

    flex : 1;
    justify-content : center;

    @include mq($until: desktop) {
        padding:10px;
    }
}

.e-footer__element--header {
    h3 {
        color : $accent;

        @include word-shade;
    }
}
