@mixin gradientBg($direction, $startColor, $endColor) {
	background: $startColor;
	background: -moz-linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
	background: -webkit-linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
	background: linear-gradient($direction,  $startColor 0%, $endColor 80%,$endColor 100%);
}

@mixin box-shadow($top, $left, $blur, $color, $inset: false) {
  @if $inset {
    -webkit-box-shadow:inset $top $left $blur $color;
    -moz-box-shadow:inset $top $left $blur $color;
    box-shadow:inset $top $left $blur $color;
  } @else {
    -webkit-box-shadow: $top $left $blur $color;
    -moz-box-shadow: $top $left $blur $color;
    box-shadow: $top $left $blur $color;
  }
}

@mixin transformed {
  transform: rotate(45deg);
}

@mixin alt-transformed {
  transform: rotate(-45deg);
}

@mixin shaded {
  @include box-shadow(8px , 10px ,20px, rgba(46,61,73, .15));
}

@mixin mild-shade {
  @include box-shadow(8px , 10px ,20px, rgba(46,61,73,0.3));
}

@mixin dark-shade {
  @include box-shadow(8px , 10px ,20px, rgba(46,61,73, .6));
}

@mixin hover-shade{
  @include box-shadow(2px , 4px ,8px, rgba(46,61,73,0.2));
}

@mixin word-shade {
  text-shadow:8px  10px 20px rgba(46,61,73,0.4);
}

@mixin gradient {
  transition: box-shadow .3s ease-in, background-color .3s ease-in;
  
  &:hover {
    @include gradientBg(-45deg, $accent, $accent-lighter);
  }
  &:focus, &:active {
    @include gradientBg(-45deg, $accent-lighter, $accent);
  }
  &:focus {
    // outline: 2px solid $neon;
    // box-shadow: 5px 8px 20px 0px rgba(59,52,33,.3);
    @include mild-shade;
  }
  &:active {
    filter: brightness(.85) contrast(1.3);
  }
}

@mixin bg-gradient {
  @include gradientBg(-45deg, $accent-lighter, $accent-lighting);
}

@mixin gradient--reversed {
  transition: box-shadow .3s ease-in, background-color .3s ease-in;
  @include gradientBg(-45deg, $accent, $accent-lighter);
  
  &:hover {
    @include gradientBg(-45deg, $primary, $primary-lighting);
    @include hover-shade;
  }
  &:focus, &:active {
    @include gradientBg(-45deg, $primary-lighting, $primary);
  }
  &:focus {
    // outline: 2px solid $neon;
    // box-shadow: 5px 8px 20px 0px rgba(59,52,33,.3);
    @include mild-shade;
  }
  &:active {
    filter: brightness(.85) contrast(1.3);
  }
}

@mixin alt-gradient {
  @include gradientBg(130deg, $primary, $primary-lighting);
}

@mixin alt-gradient-transformed {
  @include gradientBg(180deg, $primary, $primary-lighting);
}