.c-box {
    font-size : 16px;
    // background-color: $accent;
    // overflow: hidden;

    z-index : 10;

    width : 100px;
    height : 100px;

    cursor : pointer;

    border-radius : 10px;

    @include mild-shade;
    @include alt-gradient;
    @include mq($until : tablet) {
        width : 80px;
        height : 80px;

        @include transformed;
    }
}

.c-box:hover {
    background-color : $accent;
    // -webkit-transform: translate(-5%,-5%);
    // transform: translate(-5px,-5px);
    // box-shadow: 5px 8px 20px 0px rgba(59,52,33,.3);

    @include hover-shade;
}


.c-box--left {
    position : absolute;
    right : auto;
    left : 0;
}

.c-box--right {
    position : absolute;
    right : 0;
    left : auto;
}

.c-box--top {
    position : absolute;
    top : 0;
    bottom : auto;
}

.c-box--bottom {
    position : absolute;
    top : auto;
    bottom : 0;
}

.c-box__primary {
    font-size : 16px;

    height : 19.5em;

    border-radius : 10px;
    /* width: 300px;*/
    background : $primary;
    @include mild-shade;

    @include mq($until : tablet) {
        position : absolute !important;
        top : 0;
        right : 0;
        bottom : 0;
        left : 0;

        margin : auto !important;

        @include transformed;
    }
    @include mq($from : tablet, $until:desktop) {
        height : inherit !important;
    }
    @include mq($until : desktop) {
        width : 80px;
        height : 80px;
    }

    .e-image-box {
    display: block;
    position: relative;

    &:after {
        content: '';
    background: #00bcd4;
    opacity: 0.4;
    width: 500px;
    height: 500px;
    z-index: 100;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    }
}

}

.c-box:hover .c-box__tooltip {
    display : block;
    // animation : flip .6s ease-in reverse both;

    opacity : 1;
}

.c-box__tooltip {
    position : absolute;
    z-index : 100;
    top : -1em;
    left : 4.5em;

    width : 100%;
    padding : 5px 10px;

    transition : all .3s ease-in;
    -webkit-transform : rotate(-45deg);
            transform : rotate(-45deg) skewX(-45deg);
    text-decoration : none;

    opacity : 0;
    border-radius : 4px;
    background-color : $primary;

    @include dark-shade;
    @include mq($until : tablet) {
        top : 6em;
        bottom : auto;
        left : 4.25em;

        margin : auto;

        transform : rotate(-45deg) skewX(0);

        opacity : 1;
        @include mild-shade;
    }
    &:hover {
        @include hover-shade;
    }
}

.c-box__tooltip span {
    font-size : 14px;

    display : block;

    transition : all .3s ease-in;
    transform : skewX(45deg);

    color : $base;

    @include mq($until : tablet) {
        transform : skewX(0);
        color: $highlight;
    }
}



.c-boxed {
    position : relative;

    width : 30px;
    height : 30px;

    border-radius : 5px;

    @include alt-gradient-transformed;
    @include mild-shade;
    // background-color: $accent;
    &:before , &:after {
        position : absolute;
        // content : ' ';

        @include mild-shade;
    }
}

.c-boxed:before {
    width : 10em;
    height : .5em;
}

.c-boxed:after {
    width : .5em;
    height : 10em;
}

.o-wrapper .o-row:nth-child(2) .col-1:nth-child(3) .c-box__tooltip {
    @include mq($until : tablet) {
        top : auto;
        right : 4.25em;
        bottom : 6em;
        left : auto;
    }
}

.o-wrapper .o-row:last-child .c-box__tooltip {
    @include mq($until : tablet) {
        top : auto;
        right : 4.25em;
        bottom : 6em;
        left : auto;
    }
}
